import React from 'react';
import {  } from 'semantic-ui-react';

export default class ProcedureCard extends React.Component {
  render() {
    return <div className={ `procedure-card ${this.props.className}` }>
             <div className='content'>
               <div className='title'>
                 { this.props.title }
               </div>
             </div>
           </div>;
  }
}



