import React from 'react';
import Button from '../../Button.jsx';
import {
  Grid, Container, Image } from 'semantic-ui-react';
import image from '../../../../assets/images/klari.jpg';
import getText from '../../../utils/getText.js';

export default class About extends React.Component {
  getParagraphs() {
    var texts = getText('index.about.paragraphs');
    var paragraphs = [];

    for (var i = 0; i < texts.length; i++) {
      paragraphs.push(<p key={i + texts[i].substring(10)}>
                        { texts[i] }
                      </p>);
    }

    return paragraphs;
  }

  render() {
    return <section id='about'>
             <Container>
               <Grid stackable columns={ 2 }>
                 <Grid.Column className='text'>
                   <div className='content'>
                     <h3 className='heading'>
                       { getText('index.about.title') }
                     </h3>
                     <div className='subtitle'>
                       { getText('index.about.subtitle') }
                     </div>
                     { this.getParagraphs() }
                     <div className='button-container'>
                       <Button primary type='appointment' />
                       <Button type='learnMore' />
                     </div>
                   </div>
                 </Grid.Column>
                 <Grid.Column>
                   <div className='content'>
                     <Image src={ image }></Image>
                   </div>
                 </Grid.Column>
               </Grid>
             </Container>
           </section>;
  }
}



