import React from 'react';
import Button from '../../Button.jsx';
import { Grid, Container, Card } from 'semantic-ui-react';
import ProcedureCard from './ProcedureCard.jsx';
import getText from '../../../utils/getText.js';

export default class Procedures extends React.Component {
  render() {
    return <section id='procedures'>
             <Container>
               <Grid stackable stretched columns={ 4 }>
                 <Grid.Column width={ 8 }>
                   <div className='content'>
                     <h2 className='heading'>{ getText('index.procedures.title') }</h2>
                     <div className='button-container'>
                       <Button type='priceList'></Button>
                     </div>
                   </div>
                 </Grid.Column>
                 <Grid.Column width={ 4 }>
                   <ProcedureCard className='rhino' title={ getText('procedures.nose') }></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column width={ 4 }>
                   <ProcedureCard className='breast' title={ getText('procedures.breast') }></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column width={ 4 }>
                   <ProcedureCard className='ear' title={ getText('procedures.ear') }></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column width={ 4 }>
                   <ProcedureCard className='wrinkle' title={ getText('procedures.wrinkle') }></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column width={ 4 }>
                   <ProcedureCard className='eyelid' title={ getText('procedures.eye') }></ProcedureCard>
                 </Grid.Column>
               </Grid>;
               <Grid stackable stretched centered className='special'>
                 <Grid.Column>
                   <ProcedureCard className='' title={ getText('procedures.sweat') }></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column>
                   <ProcedureCard className='' title={ getText('procedures.prp') }></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column>
                   <ProcedureCard className='' title={ getText('procedures.baldness') }></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column>
                   <ProcedureCard className='' title={ getText('procedures.skin') }></ProcedureCard>
                 </Grid.Column>
                 <Grid.Column>
                   <ProcedureCard className='' title={ getText('procedures.mezo') }></ProcedureCard>
                 </Grid.Column>
               </Grid>
             </Container>
           </section>;
  }
}



