import React from 'react';
import Button from '../../Button.jsx';
import { Grid, Container } from 'semantic-ui-react';
import image from '../../../../assets/images/rhino1.jpg';
import getText from '../../../utils/getText.js';
import Waypoint from 'react-waypoint';

export default class Noseart extends React.Component {
  getParagraphs() {
    var texts = getText('index.noseart.paragraphs');
    var paragraphs = [];

    for (var i = 0; i < texts.length; i++) {
      paragraphs.push(<p key={ i + texts[i].substring(10) }>
                        { texts[i] }
                      </p>);
    }

    return paragraphs;
  }

  showStickyHeader() {
    console.log('show');
  }

  render() {
    return <section id='noseart'>
             <Container>
               <Grid stackable columns={ 2 }>
                 <Grid.Column className='img'>
                   <div className='content'>
                   </div>
                 </Grid.Column>
                 <Grid.Column className='text'>
                   <div className='content'>
                     <h2 className='heading'>
                                                 { getText('index.noseart.title') }
                                               </h2>
                     <div className='subtitle'>
                       { getText('index.noseart.subtitle') }
                     </div>
                     { this.getParagraphs() }
                     <div className='button-container'>
                       <Button primary type='appointment' />
                       <Button type='learnMore' />
                     </div>
                   </div>
                 </Grid.Column>
               </Grid>
             </Container>
           </section>;
  }
}



